import React from "react"
import BaseComponent from '../../../components/BaseComponent'
import { Col, Row, Tabs } from 'antd'

const { TabPane } = Tabs;

export class Help extends BaseComponent {
    constructor(props) {
        super(props)
        this.state = {
            activeKey: '1',
            languageTips: 'DockerVSCodeDocker',
            pageTips: 'CodePass',
        }
    };

    handlePageJump = () => {
        this.setState({
            activeKey: '2'
        })
    }

    keyChange = (key)=>{
        this.setState({
            activeKey:key
        })
    }

    render() {
        return (
            <Row style={styles.container}>
                <Col lg={6} xs={1} />
                <Col lg={12} xs={22}>
                    <Tabs activeKey={this.state.activeKey} onChange={this.keyChange}>
                        <TabPane tab="" key="1">
                            <Row>
                                <h1 style={styles.title}>CodePass</h1>
                            </Row>
                            <Row>
                                <p style={styles.paragraph}>{this.state.pageTips}</p>
                            </Row>
                            <Row>
                                <h1 style={styles.title}></h1>
                            </Row>
                            <Row>
                                <p style={styles.paragraph}>?dockerdocker
                                 <a onClick={this.handlePageJump}>Docker</a></p>
                            </Row>
                        </TabPane>
                        <TabPane tab="Docker" key="2">
                            <Row>
                                <h1 style={styles.title}>Docker</h1>
                            </Row>
                            <Row>
                                <p style={styles.paragraph}>{this.state.languageTips}</p>
                            </Row>
                            <Row>
                                <h1 style={styles.title}>Docker</h1>
                            </Row>
                            <Row>
                                <p style={styles.paragraph}><b>C++</b><b>Java</b><b>Python3</b>Docker</p>
                                <p style={styles.paragraph}></p>
                            </Row>
                        </TabPane>
                    </Tabs>
                </Col>
            </Row>
        )
    }
}

const styles = {
    container: {
        marginTop: "50px",
        paddingBottom: "100px"
    },
    paragraph: {
        fontSize: "20px",
        fontFamily: "Arial,'Microsoft Yahei',''",
        // fontWeight:'600',
        lineHeight: '30px',
        textIndent: '2em',
    },
    title: {
        fontSize: "32px",
    },
    link: {
        textDecoration: 'underline',
        color: '#00FFFF',
    }
}

