import { Button, Col, Input, Radio, Select, InputNumber } from 'antd';
import React from 'react'
import BaseComponent from '../../../components/BaseComponent';
import { EditTwoTone, PlusCircleTwoTone } from '@ant-design/icons'
import './css/data.css'

const { Option } = Select

export default class Data extends BaseComponent {
    constructor(props) {
        super(props)
        this.state = {
            display_btn: false,
            display_msg: true,
            save_btn_disabled: true,
            input: null,
        }
    }

    render() {
        return (
            <Col className='each-div'>
                <Col span={6} className='title-div'>
                    <h3 className='title'>{this.props.name}</h3>
                </Col>
                {this.state.display_msg ? this.renderRow() : this.renderModifyData()}
            </Col>
        )
    }

    renderRow() {
        if (this.props.content === null || this.props.content === 'null') {
            return (
                <Col className='msg' span={18} onMouseOver={() => this.handleMouseOver()} onMouseOut={() => this.handleMouseOut()}>
                    <span className='msg-text'></span>
                    <Button
                        type='default'
                        size='default'
                        className='modify-btn'
                        style={{ display: this.state.display_btn ? 'inline-block' : 'none' }}
                        onClick={() => this.modifyButtonClick()}
                    >
                        <PlusCircleTwoTone></PlusCircleTwoTone>
                        <span className='change-text'></span>
                    </Button>
                </Col>
            )
        }
        else {
            return (
                <Col className='msg' span={18} onMouseOver={() => this.handleMouseOver()} onMouseOut={() => this.handleMouseOut()}>
                    <span className='msg-text'>{this.props.content}</span>
                    <Button
                        type='default'
                        size='default'
                        className='modify-btn'
                        style={{ display: this.state.display_btn ? 'inline-block' : 'none' }}
                        onClick={() => this.modifyButtonClick()}
                    >
                        <EditTwoTone></EditTwoTone>
                        <span className='change-text'></span>
                    </Button>
                </Col>
            )
        }
    }

    handleMouseOver() {
        if (this.props.enable_change) {
            this.setState({
                display_btn: true
            })
        }
    }

    handleMouseOut() {
        if (this.props.enable_change) {
            this.setState({
                display_btn: false
            })
        }
    }

    modifyButtonClick = () => {
        this.setState({
            display_msg: false
        })
    }

    cancelButtonClick = () => {
        this.setState({
            display_msg: true
        })
    }

    inputChange = (value) => {
        this.setState({
            save_btn_disabled: false,
            input: value
        })
    }

    radioChecked = () => {
        var radios = document.getElementById('radioGroup').children
        for (let i = 0; i < radios.length; i++) {
            if (radios[i].checked) {
                return radios[i].value
            }
        }
    }

    renderModifyData() {
        switch (this.props.name) {
            case (''):
                return (
                    <Col span={18}>
                        <Input
                            placeholder={this.props.content ? this.props.content : ''}
                            style={{ width: '50%' }} onChange={(e) => { this.inputChange(e.target.value) }}
                        />
                        <Button
                            type='primary'
                            className='operate-btn'
                            disabled={this.state.save_btn_disabled ? true : false}
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ name: { name: '', content: this.state.input ? this.state.input : null }, change: 0 })}}
                        ></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            case (''):
                return (
                    <Col span={18}>
                        <Radio.Group buttonStyle='solid' onChange={(e) => this.inputChange(e.target.value)}>
                            <Radio.Button value=''></Radio.Button>
                            <Radio.Button value=''></Radio.Button>
                        </Radio.Group>
                        <Button
                            type='primary'
                            className='operate-btn'
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ gender: { name: '', content: this.state.input ? this.state.input : null }, change: 1 })}}
                        ></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            case (''):
                return (
                    <Col span={18}>
                        <Input
                            placeholder={this.props.content !== null && this.props.content !== 'null' ? this.props.content : ''}
                            style={{ width: '50%' }} onChange={(e) => { this.inputChange(e.target.value) }}
                        />
                        <Button
                            type='primary'
                            className='operate-btn'
                            disabled={this.state.save_btn_disabled ? true : false}
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ job: { name: '', content: this.state.input ? this.state.input : null }, change: 2 })}}
                        ></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            case (''):
                return (
                    <Col span={18}>
                        <Select style={{ width: '50%' }}
                            defaultValue={this.state.input ? this.state.input : ''}
                            onChange={e => this.inputChange(e)}
                        >
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                        </Select>
                        <Button type='primary' className='operate-btn'
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ tech: { name: '', content: this.state.input ? this.state.input : '' }, change: 3 })}}></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            case (''):
                return (
                    <Col span={18}>
                        <InputNumber min={1} max={100} style={{ width: '50%' }} onChange={(e) => { this.inputChange(e) }} placeholder={this.props.content !== null && this.props.content !== 'null' ? this.props.content : ''}></InputNumber>
                        <Button type='primary' className='operate-btn' disabled={this.state.save_btn_disabled ? true : false}
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ age: { name: '', content: this.state.input ? this.state.input : null }, change: 4 })}}></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            case (''):
                return (
                    <Col span={18}>
                        <Input
                            placeholder={this.props.content ? this.props.content : ''}
                            style={{ width: '50%' }} onChange={(e) => { this.inputChange(e.target.value) }}
                        />
                        <Button
                            type='primary'
                            className='operate-btn'
                            disabled={this.state.save_btn_disabled ? true : false}
                            onClick={() => {this.cancelButtonClick();this.props.onChangeState({ job: { name: '', content: this.state.input ? this.state.input : null }, change: 2 })}}
                        ></Button>
                        <Button onClick={() => { this.cancelButtonClick() }} className='operate-btn'></Button>
                    </Col>
                )
            default:
                return;
        }
    }
}